import './Preguntas.css';
import { db } from '../../db/data.js'; //complete list
// import { db } from '../../db/favs.js';  //current favorite list
import { Pregunta } from './pregunta/Pregunta.jsx';

export function Preguntas(props) {

  const lista = [props.op1, props.op2, props.op3, props.op4]
  var correcto = lista[Math.floor(Math.random() * lista.length)];

  return (
    <div>
      <h3>{db[correcto].english}</h3>
      <p>{db[correcto].type}</p>
      <div className='preguntas'>
        <Pregunta datos={db[props.op1]} corr={db[correcto]} />
        <Pregunta datos={db[props.op2]} corr={db[correcto]} />
        <Pregunta datos={db[props.op3]} corr={db[correcto]} />
        <Pregunta datos={db[props.op4]} corr={db[correcto]} />
      </div>
    </div>
  )
}