import { Preguntas } from '../preguntas/Preguntas.jsx';
import './Quiz.css';
import { db } from '../../db/data.js'; //complete list
// import { db } from '../../db/favs.js';  //current favorite list
import { useEffect, useState } from 'react';

export function Quiz() {
  const [n, setN] = useState(0);
  const [n2, setN2] = useState(1);
  const [n3, setN3] = useState(2);
  const [n4, setN4] = useState(3);

  const randoms = () => {
    setN(Math.floor(Math.random() * db.length))
    setN2(Math.floor(Math.random() * db.length))
    setN3(Math.floor(Math.random() * db.length))
    setN4(Math.floor(Math.random() * db.length))
  }

  useEffect(() => { randoms() }, []);


  return (
    <div className="quiz">
      <Preguntas
        op1={n}
        op2={n2}
        op3={n3}
        op4={n4}
      />
    </div >
  )
}
