import './Pregunta.css';
import Swal from 'sweetalert2';

export function Pregunta(props) {

  //Correctos:
  var trad = props.datos.spanish;
  var word = props.corr.english;

  //Errores:
  var errTrad = props.corr.spanish;

  return (
    <div className='pregunta'
      onClick={() => {
        if (props.datos.spanish === props.corr.spanish) {

          Swal.fire({
            title: word,
            text: trad,
            // timer: 10000,
            icon: 'success',
            iconColor: '#0ccc76',
            timerProgressBar: true,
            confirmButtonColor: '#0e5c5e',
            color: '#000',
            confirmButtonText: 'Siguiente'
          }).then(function () {
            window.location.reload();
          })

        }
        else {
          Swal.fire({
            title: word,
            text: errTrad,
            // timer: 10000,
            icon: 'error',
            iconColor: '#f33',
            timerProgressBar: true,
            confirmButtonColor: '#0e5c5e',
            color: '#000',
            confirmButtonText: 'Siguiente'
          }).then(function () {
            window.location.reload();
          })

        }
      }}
    >
      {props.datos.spanish}
    </div>
  )
}